"use client"

import { useState, useEffect } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Button } from "@/components/ui/button"
import { Loader2, Clock, Plus, LayoutGrid } from "lucide-react"

export default function ProductFormModal({ isOpen, onClose, initialData, onSuccess }: any) {
  const [isLoading, setIsLoading] = useState(false)
  
  // 1. Ìèº Ï¥àÍ∏∞ ÏÉÅÌÉú (Î∞±ÏóîÎìú ProductRequest Í∑úÍ≤© Í∏∞Î∞ò)
  const [formData, setFormData] = useState({
    productName: "",
    description: "",
    category: "",
    durationMin: 60,
    restTime: 15,
    workingHours: 3,
    startTime: 1400,
    endTime: 1700,
    price: "" as any,
    dayOfWeek: "MONDAY"
  })

  useEffect(() => {
    if (initialData) {
      setFormData(initialData)
    } else {
      setFormData({
        productName: "",
        description: "",
        category: "",
        durationMin: 60,
        restTime: 15,
        workingHours: 3,
        startTime: 1400,
        endTime: 1700,
        price: "" as any,
        dayOfWeek: "MONDAY"
      })
    }
  }, [initialData, isOpen])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    const token = localStorage.getItem("accessToken")
    const method = initialData ? "PUT" : "POST"
    
    // PathVariableÎ°ú ÏÇ¨Ïö©Îê† productId
    const url = initialData 
      ? `http://34.50.7.8:3000/v1/products/${initialData.productId}`
      : "http://34.50.7.8:3000/v1/products"

    try {
      /**
       * 2. Ï†ÑÏÜ° Îç∞Ïù¥ÌÑ∞ Ï°∞Î¶Ω (Request Body)
       * Î∞±ÏóîÎìú ProductRequest ÌÅ¥ÎûòÏä§Ïóê Ï†ïÏùòÎêú 10Í∞ú ÌïÑÎìúÎßå Ï†ïÌôïÌûà Îß§ÌïëÌï©ÎãàÎã§.
       * Ïù¥Î†áÍ≤å ÌïòÎ©¥ reserved, advisorId, productId Îì±Ïù¥ Ìè¨Ìï®ÎêòÏñ¥ Î∞úÏÉùÌïòÎäî ÏóêÎü¨Î•º ÏõêÏ≤ú Ï∞®Îã®Ìï©ÎãàÎã§.
       */
      const payload = {
        productName: formData.productName,
        description: formData.description,
        category: formData.category,
        durationMin: Number(formData.durationMin),
        restTime: Number(formData.restTime),
        workingHours: Number(formData.workingHours),
        startTime: Number(formData.startTime),
        endTime: Number(formData.endTime),
        price: Number(formData.price),
        dayOfWeek: formData.dayOfWeek
      }

      const res = await fetch(url, {
        method,
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        },
        body: JSON.stringify(payload)
      })

      if (res.ok) {
        onSuccess()
        onClose()
      } else {
        const result = await res.json()
        alert(`Ïã§Ìå®: ${result.message || "Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§."}`)
      }
    } catch (error) {
      alert("ÏÑúÎ≤ÑÏôÄ ÌÜµÏã† Ï§ë ÏóêÎü¨Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-xl rounded-xl border border-border p-0 overflow-hidden bg-background shadow-2xl">
        <DialogHeader className="p-6 bg-card/50 border-b border-border">
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
              <Plus className="h-5 w-5 text-primary" />
            </div>
            <div>
              <DialogTitle className="text-xl font-bold text-foreground">
                {initialData ? "ÏÉÅÎã¥ ÏÉÅÌíà ÏàòÏ†ï" : "ÏÉà ÏÉÅÌíà Îì±Î°ù"}
              </DialogTitle>
              <p className="text-xs text-muted-foreground font-medium">ÏÉÅÎã¥ ÏÑúÎπÑÏä§Ïùò ÏÉÅÏÑ∏ Í∑úÍ≤©ÏùÑ ÏÑ§Ï†ïÌï©ÎãàÎã§.</p>
            </div>
          </div>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {/* ÏÑπÏÖò 1: Í∏∞Î≥∏ Ï†ïÎ≥¥ */}
          <div className="space-y-4">
            <h3 className="text-sm font-bold flex items-center gap-2 text-foreground">
              <LayoutGrid className="h-4 w-4 text-primary" /> Í∏∞Î≥∏ Ï†ïÎ≥¥
            </h3>
            <div className="bg-card border border-border rounded-xl p-5 space-y-4 shadow-sm">
              <div className="space-y-2">
                <Label className="text-xs font-bold text-muted-foreground uppercase">ÏÉÅÎã¥ ÏÉÅÌíàÎ™Ö</Label>
                <Input value={formData.productName} onChange={e => setFormData({...formData, productName: e.target.value})} className="bg-background border-border" required />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label className="text-xs font-bold text-muted-foreground uppercase">Ïπ¥ÌÖåÍ≥†Î¶¨</Label>
                  <Input value={formData.category} onChange={e => setFormData({...formData, category: e.target.value})} className="bg-background border-border" required />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs font-bold text-muted-foreground uppercase">ÏÉÅÎã¥ Í∞ÄÍ≤© (‚Ç©)</Label>
                  <Input type="number" value={formData.price} onChange={e => setFormData({...formData, price: e.target.value})} className="bg-background border-border font-bold text-primary" required />
                </div>
              </div>
              <div className="space-y-2">
                <Label className="text-xs font-bold text-muted-foreground uppercase">ÏÉÅÎã¥ ÏÑ§Î™Ö</Label>
                <Textarea value={formData.description} onChange={e => setFormData({...formData, description: e.target.value})} className="bg-background border-border min-h-[80px] resize-none" required />
              </div>
            </div>
          </div>

          {/* ÏÑπÏÖò 2: Ïä§ÏºÄÏ§Ñ ÏÑ§Ï†ï */}
          <div className="space-y-4">
            <h3 className="text-sm font-bold flex items-center gap-2 text-foreground">
              <Clock className="h-4 w-4 text-primary" /> Ïä§ÏºÄÏ§Ñ ÏÑ§Ï†ï
            </h3>
            <div className="bg-secondary/20 border border-border rounded-xl p-5 grid grid-cols-3 gap-4">
              <div className="space-y-2">
                <Label className="text-[10px] font-bold text-muted-foreground uppercase tracking-tight">ÏÉÅÎã¥ ÏöîÏùº</Label>
                <select 
                  value={formData.dayOfWeek} 
                  onChange={e => setFormData({...formData, dayOfWeek: e.target.value})} 
                  className="w-full h-9 rounded-md border border-border bg-background text-xs font-bold px-2 outline-none"
                >
                  {["MONDAY", "TUESDAY", "WEDNESDAY", "THURSDAY", "FRIDAY", "SATURDAY", "SUNDAY"].map(day => (
                    <option key={day} value={day}>{day}</option>
                  ))}
                </select>
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-bold text-muted-foreground uppercase tracking-tight">ÏÉÅÎã¥(Î∂Ñ)</Label>
                <Input type="number" value={formData.durationMin} onChange={e => setFormData({...formData, durationMin: Number(e.target.value)})} className="h-9 bg-background border-border text-xs font-bold" />
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-bold text-muted-foreground uppercase tracking-tight">Ìú¥Ïãù(Î∂Ñ)</Label>
                <Input type="number" value={formData.restTime} onChange={e => setFormData({...formData, restTime: Number(e.target.value)})} className="h-9 bg-background border-border text-xs font-bold" />
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-bold text-muted-foreground uppercase tracking-tight">ÏãúÏûë(HHMM)</Label>
                <Input type="number" value={formData.startTime} onChange={e => setFormData({...formData, startTime: Number(e.target.value)})} className="h-9 bg-background border-border text-xs font-bold" />
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-bold text-muted-foreground uppercase tracking-tight">Ï¢ÖÎ£å(HHMM)</Label>
                <Input type="number" value={formData.endTime} onChange={e => setFormData({...formData, endTime: Number(e.target.value)})} className="h-9 bg-background border-border text-xs font-bold" />
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-bold text-muted-foreground uppercase tracking-tight">ÏùºÏùºÍ±¥Ïàò</Label>
                <Input type="number" value={formData.workingHours} onChange={e => setFormData({...formData, workingHours: Number(e.target.value)})} className="h-9 bg-background border-border text-xs font-bold" />
              </div>
            </div>
          </div>

          <div className="flex items-center gap-3 pt-2">
            <Button type="button" variant="ghost" onClick={onClose} className="flex-1 font-bold text-muted-foreground">Ï∑®ÏÜå</Button>
            <Button type="submit" disabled={isLoading} className="flex-[2] font-bold bg-primary hover:bg-primary/90 text-primary-foreground shadow-lg shadow-primary/20">
              {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              {initialData ? "ÏàòÏ†ï ÏôÑÎ£å" : "ÏÉÅÌíà Îì±Î°ù ÏôÑÎ£å"}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}